import React from 'react';
import styled from 'styled-components';

export default function Timeline () {

    // data
    const data = [
        {
            id: 0,
            year: 2010,
            title: 'title 1',
            text: 'This is a text here blablablabla flou flou flou riri ri ri ri',
        },
        {
            id: 1,
            year: 2010,
            title: 'title 1',
            text: 'This is a text here blablablabla flou flou flou riri ri ri ri',
        },
        {
            id: 2,
            year: 2011,
            title: 'title 1',
            text: 'This is a text here blablablabla flou flou flou riri ri ri ri',
        },
        {
            id: 3,
            year: 2012,
            title: 'title 1',
            text: 'This is a text here blablablabla flou flou flou riri ri ri ri',
        }
    ]

    const Container = styled.div`
        display: flex;
        height: 400px;
        width: 100%;
        position: relative;
        justify-content: center;
    `;

    const Column = styled.div`
        height: 100%;
        width: 75px;
        background-color: red;
        border-right: 1px solid black;
    `;

    const LineMiddle = styled.div`
        width: 100%;
        top: 50%;
        position: absolute;
        border-bottom: 1px solid black;

    `;

    const Year = styled.span`
        font-size: 24px;
        position: absolute;
        top: 50%;
        margin: 0;
        font-size
    `;

    const Card = styled.div`
        width: 225px;
        background-color:white;
        position:absolute;
        left: ${propsS => propsS.x || 0}%;
        top: ${propsS => propsS.y || 0}%;
    `

    function getColumn() {

        const positions = [
            { y: 25 / 2},
            { y: 25 + 25/2},
            { y: 75 + 25/2},
            { y: 50 + 25/2},
        ]
        const years = {};
        return data.map((d, index)=>{
            const year = years[d.year] ? '' : d.year;
            years[d.year] = true;

            return (
            <Column key={d.id}>
                <Card x={0} y={positions[index % 4].y}>
                    <h5>{d.title}</h5>
                    <p>{d.text}</p>
                </Card>
                <Year> {year} </Year>
            </Column>
            );
        });
    }

    return (
        <Container>
            {getColumn()}
            <LineMiddle />
        </Container>
    );
}